/*--------------------------------------------------------------
## Login
--------------------------------------------------------------*/
body.login div#login h1 a, body.login div#login .h1 a {
  background-image: url(https://codearosa.de/wp-content/uploads/2018/12/Favicon-Codearosa.png);
  padding-bottom: 30px; }

body.login {
  background-color: #FBFBFD; }

p#backtoblog {
  display: none; }

#loginform {
  background-color: #FBFBFD;
  border-radius: 0.5rem; }

.login #backtoblog a,
.login #nav a {
  color: #C8689A; }

.wp-core-ui .button-primary,
.wp-core-ui .button-primary:hover,
.wp-core-ui .button-primary:focus,
.wp-core-ui .button-primary:active {
  width: 100%;
  transform: none !important;
  background-color: #FBFBFD;
  color: #C8689A !important;
  text-transform: uppercase !important;
  margin-top: 2em;
  font-size: large; }

.dashicons {
  color: #C8689A !important; }

form,
input {
  background-color: #FBFBFD !important;
  border: 1px solid #C8689A;
  background-image: none !important;
  color: black !important; }

.login #login_error {
  border-left-color: #C8689A; }

input:focus,
textarea,
button:focus {
  border: none !important; }

.login #backtoblog a,
.login #nav a {
  color: #C8689A; }
