#wrapper-footer {
   @extend .px-5;

   .col-md-4 {
      @extend .pt-1;
   }

   .col-md-4:nth-child(1) {
      @extend .text-md-left;
   }

   
   .col-md-4:nth-child(2) {
      @extend .lead;
      @extend .text-dark;
      @extend .text-center;

      a {
         @extend .text-dark;

         &:hover {
            @extend .text-primary;
            @extend .text-decoration-none;
         }
      }
   }

   .col-md-4:nth-child(3) {
      @extend .text-md-right;
   }

   .socialmenuclass {
      .fa,
      .fas,
      .fab {
         @extend .my-0;
         @extend .text-primary;
      }

      .nav-link {
         padding: 0 0.5em;
         line-height: 0;
      }
   }
}
